html {
    font-size: 62.5%;
    box-sizing: border-box;
    background-color: $very_light_gray;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $primary_font;
    font-size: 1.6rem;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

h1, h2, h3 {
    margin: 0;
}

button {
    border: none;
}

.container {
    width: 95%;
    max-width: 160rem;
    margin: 0 auto;
    margin-top: 7rem;
}

.text-center {
    text-align: center;
}