header {
    nav {
        display: flex;
        justify-content: space-between;
        padding: 2rem;
        padding-left: 13rem;
        padding-right: 13rem;
        background-color: $white;
        box-shadow: 0px 3px 13px -6px rgba(0,0,0,0.47);
        h1 {
            cursor: pointer;
            font-size: 3rem;
        }
        .style-button-toggle {
            display: flex;
            align-items: center;
            gap: 1rem;
            cursor: pointer;
            img {
                width: 2rem;
                height: 2rem;
            }
            span {
                margin: 0;
                padding: 0;
                font-weight: $extra-bold;
            }
        }
    }
}