/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  background-color: #fafafa;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.6rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
}

button {
  border: none;
}

.container {
  width: 95%;
  max-width: 160rem;
  margin: 0 auto;
  margin-top: 7rem;
}

.text-center {
  text-align: center;
}

header nav {
  display: flex;
  justify-content: space-between;
  padding: 2rem;
  padding-left: 13rem;
  padding-right: 13rem;
  background-color: white;
  box-shadow: 0px 3px 13px -6px rgba(0, 0, 0, 0.47);
}
header nav h1 {
  cursor: pointer;
  font-size: 3rem;
}
header nav .style-button-toggle {
  display: flex;
  align-items: center;
  gap: 1rem;
  cursor: pointer;
}
header nav .style-button-toggle img {
  width: 2rem;
  height: 2rem;
}
header nav .style-button-toggle span {
  margin: 0;
  padding: 0;
  font-weight: 800;
}

.options-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.options-bar .searchbar input {
  border: none;
  padding: 2.5rem;
  box-shadow: 0px 0px 13px 0px rgba(0, 0, 0, 0.16);
  border-radius: 0.8rem;
}
.options-bar .filter-selector select {
  border: none;
  padding: 2.2rem;
  box-shadow: 0px 0px 13px 0px rgba(0, 0, 0, 0.16);
  border-radius: 0.8rem;
}

.countries-container {
  display: flex;
  gap: 10rem;
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.card {
  display: flex;
  flex-direction: column;
  width: 30rem;
  background-color: white;
  box-shadow: 0px 0px 13px 0px rgba(0, 0, 0, 0.47);
  border-radius: 1rem;
  overflow: hidden;
  cursor: pointer;
}
.card .card__image img {
  object-fit: cover;
  object-position: center center;
}
.card .card__content {
  padding: 1rem;
}
.card .card__content .card__content-title {
  font-weight: 800;
}
.card .card__content .card__content-subtitle {
  font-weight: 600;
}
.card .card__content .card__content-span {
  font-weight: 300;
}