.options-bar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .searchbar {
        input {
            border: none;
            padding: 2.5rem;
            box-shadow: 0px 0px 13px 0px rgba(0,0,0,0.16);
            border-radius: 0.8rem;
        }
    }
    .filter-selector {
        select {
            border: none;
            padding: 2.2rem;
            box-shadow: 0px 0px 13px 0px rgba(0,0,0,0.16);
            border-radius: 0.8rem;
        }
    }
}