.countries-container {
    display: flex;
    gap: 10rem;
    margin-top: 5rem;
    margin-bottom: 5rem;
}

.card {
    display: flex;
    flex-direction: column;
    width: 30rem;
    background-color: $white;
    box-shadow: 0px 0px 13px 0px rgba(0,0,0,0.47);
    border-radius: 1rem;
    overflow: hidden;
    cursor: pointer;
    .card__image {
        img {
            object-fit: cover;
            object-position: center center;
        }
    }
    .card__content {
        padding: 1rem;
        .card__content-title {
            font-weight: $extra-bold;
        }
        .card__content-subtitle {
            font-weight: $semi-bold;
        }
        .card__content-span {
            font-weight: $light;
        }
    }
    
}